import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import axios from "axios";
import classes from "./ProductRate.module.css";
import { API_URL } from "../../../../constants";

const ProductRate = (props) => {
  /*  */
  const userInfo = useSelector((state) => state.login.userInfo);

  /*  */
  const [rating, setRating] = useState(5); // 
  const [comment, setcomment] = useState(""); // 
  const [rateExists, setRateExists] = useState(false); // ()

  /*  */
  const ratingChangeHandler = (e) => {
    setRating(parseInt(e.target.value));
  };
  /*  */
  const commentChangeHandler = (e) => {
    setcomment(e.target.value);
  };

  /*  */
  useEffect(() => {
    if (userInfo) {
      const fetchRate = async () => {
        try {
          const response = await axios.get(
            `${API_URL}/products/api/product_reviews_select/${props.product_id}?user_id=${userInfo.user_id}`
          );
          if (response.data.rating) {
            setRating(response.data.rating);
            setcomment(response.data.comment);
            setRateExists(true);
          } else {
            setRating(5);
            setcomment("");
            setRateExists(false);
          }
        } catch (err) {
          console.error("product_reviews_select ", err);
        }
      };
      fetchRate();
    }
  }, [props.product_id]);

  /*  */
  const postRateHandler = async () => {
    try {
      const response = await axios.post(
        `${API_URL}/products/api/product_reviews_insert/${props.product_id}?user_id=${userInfo.user_id}`,
        { rating: rating, comment: comment }
      );
      alert(response.data.message);
      setRateExists(true);
      props.renewRating((prev) => !prev); // ProductInfo 
    } catch (err) {
      console.error("product_reviews_insert ", err);
    }
  };

  /*  */
  const updateRateHandler = async () => {
    try {
      const response = await axios.put(
        `${API_URL}/products/api/product_reviews_update/${props.product_id}?user_id=${userInfo.user_id}`,
        { rating: rating, comment: comment }
      );
      alert(response.data.message);
      props.renewRating((prev) => !prev); // ProductInfo 
    } catch (err) {
      console.error("product_reviews_update ", err);
    }
  };

  /*  */
  const deleteRateHandler = async () => {
    try {
      const response = await axios.delete(
        `${API_URL}/products/api/product_reviews_delete/${props.product_id}?user_id=${userInfo.user_id}`
      );
      alert(response.data.message);
      setRating(5);
      setcomment("");
      setRateExists(false);
      props.renewRating((prev) => !prev); // ProductInfo 
    } catch (err) {
      console.error("product_reviews_delete ", err);
    }
  };

  return (
    <div className={classes.productRate}>
      {/*  */}
      <div className={classes.rating}>
        <div>
          <span className={classes.title}></span>
          <select onChange={ratingChangeHandler} value={rating}>
            <option>1</option>
            <option>2</option>
            <option>3</option>
            <option>4</option>
            <option>5</option>
          </select>
        </div>
        <span className={classes.title}>{rating}</span>

        {rateExists ? (
          <span className={classes.rateExists}></span>
        ) : (
          <span className={classes.rateNotExists}></span>
        )}
      </div>
      {/*  */}
      <div>
        <div className={classes.comment}>
          <span className={classes.title}></span>
          <textarea
            value={comment}
            placeholder=""
            onChange={commentChangeHandler}
          />
          <div className={classes.btnContainer}>
            <div>
              <button
                type="button"
                onClick={updateRateHandler}
                className={rateExists ? null : classes.notAllow}
              >
                
              </button>
              <button
                type="button"
                onClick={deleteRateHandler}
                className={rateExists ? null : classes.notAllow}
              >
                
              </button>
            </div>
            <button
              type="button"
              onClick={postRateHandler}
              className={rateExists ? classes.notAllow : null}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductRate;
