import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { logout } from "../../../redux/loginSlice";
import { API_URL } from "../../../constants";
import axios from "axios";

import Image from "../Image/Image";
import Search from "../Search/Search";

import "./Sidebar.css";

const Sidebar = () => {
  /*  */
  const isUserLoggedIn = useSelector((state) => state.login.isUserLoggedIn);
  const userInfo = useSelector((state) => state.login.userInfo);
  const dispatch = useDispatch();

  /*  */
  const [searchState, setSearchState] = useState(false);

  /*  */
  const openSearchHandler = () => {
    setSearchState((prev) => !prev);
  };

  /* / sidebar */
  const [openSidebar, setOpenSidebar] = useState(false);
  const openSidebarHandler = () => {
    setOpenSidebar((prev) => !prev);
  };
  const location = useLocation();
  useEffect(() => {
    setOpenSidebar(false);
  }, [location.pathname, searchState, isUserLoggedIn]);

  /*  */
  const logoutHandler = async () =>
    await axios
      .get(API_URL + "/login/logout", { withCredentials: true })
      .then((response) => {
        if (response.data.message === "") {
          dispatch(logout());
          alert(response.data.message);
        }
      })
      .catch((err) => {
        console.error(":", err.response.data);
      });

  return (
    <React.Fragment>
      {/*  */}
      <div
        className={`sidebar_button ${openSidebar ? "btn_noBorder" : null}`}
        onClick={openSidebarHandler}
      >
        {openSidebar ? (
          <Image
            className="closeBar"
            src="/images/UI/common_button/cancel.svg"
            alt=""
          />
        ) : (
          <Image
            className="openBar"
            src="/images/UI/Nav/Navbar.svg"
            alt=""
          />
        )}
      </div>
      {/*  */}
      <div className={`Sidebar ${openSidebar ? "show_SideBar" : null}`}>
        {/* Logo */}
        <Link to="/" className="logo">
          <Image src="/images/UI/Nav/logo_text.svg" alt="logo" />
        </Link>
        {/*  */}
        <div className="tools">
          <div className="tool" title="" onClick={openSearchHandler}>
            <Image src="/images/UI/Nav/search.svg" alt="" />
          </div>
          <Link to="/user" className={`tool`} title="">
            <Image src="/images/UI/Nav/userInfo.svg" alt="" />
          </Link>
          {isUserLoggedIn ? (
            <div className="tool" title="" onClick={logoutHandler}>
              <img
                className="avatar"
                src={
                  userInfo.avatar
                    ? `${API_URL}/static${userInfo.avatar}`
                    : "/images/UI/User/avatar_default.jpg"
                }
                alt=""
              />
            </div>
          ) : (
            <Link to="/login" className="tool" title="">
              <Image src="/images/UI/Nav/login.svg" alt="" />
            </Link>
          )}

          {searchState ? (
            <Search
              onDialogClose={openSearchHandler}
              setSearchState={setSearchState}
            />
          ) : null}
        </div>
        {/*  */}
        <nav>
          <ul>
            <li>
              <Link to="/"></Link>
            </li>
            <li>
              <Link to="/products"></Link>
            </li>
            <li>
              <Link to="/shoppingCart"></Link>
            </li>
            <li>
              <Link to="/service"></Link>
            </li>
            <li>
              <Link to="/about"></Link>
            </li>
          </ul>
        </nav>
      </div>
    </React.Fragment>
  );
};

export default Sidebar;
