import React, { useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import { API_URL_PRODUCTS } from "../../../constants";

import ProductInfo from "./ProductInfo/ProductInfo";
import ProductFunc from "./ProductFunc/ProductFunc";

import classes from "./Product.module.css";

/*  */
const Product = () => {
  const { product_id } = useParams(); //  Params
  const navigate = useNavigate();

  /*  */
  const [product, setProduct] = useState(); // 
  /*  */
  const [averageRating, setAverageRating] = useState(); // 
  const [fetchRatingRenew, setFetchRatingRenew] = useState(false); // 

  const [typeBg, setTypeBg] = useState(); // 

  /*  */
  const getTypeBg = (type) => {
    switch (type) {
      case "":
        return "action";
      case "":
        return "avg";
      case "":
        return "slg";
      case "":
        return "strategy";
      case "":
        return "esport";
      default:
        return "";
    }
  };

  /*  */
  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const response = await axios.get(
          `${API_URL_PRODUCTS}/single_product_select/${product_id}`
        );
        if (response.data && response.data.length > 0) {
          setProduct(response.data[0]);
          setTypeBg(getTypeBg(response.data[0].product_type));
        } else {
          navigate("/404"); // 404
        }
      } catch (err) {
        console.error("single_product_select ", err);
        navigate("/404"); // 404
      }
    };
    fetchProduct();
  }, [product_id, navigate]);

  /*  */
  useEffect(() => {
    const fetchProductRating = async () => {
      try {
        const response = await axios.get(
          `${API_URL_PRODUCTS}/product_reviews_rating_select/${product_id}`
        );
        setAverageRating(response.data[0].average_rating);
      } catch (err) {
        console.error("product_reviews_rating_select ", err);
      }
    };
    fetchProductRating();
  }, [product_id, navigate, fetchRatingRenew]);

  return (
    <div className={`${classes.product} ${classes[typeBg]}`}>
      <Helmet>
        <title>TheGameC - {product ? product.product_name : ""}</title>
      </Helmet>
      <ProductInfo fetchProduct={product} averageRating={averageRating} />
      <ProductFunc
        product_id={product_id}
        fetchProduct={product}
        renewRating={setFetchRatingRenew}
      />
    </div>
  );
};

export default Product;
