import React from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

import Image from "../../../UI/Image/Image";
import classes from "./ProductSelectFunc.module.css";

const ProductSelectFunc = (props) => {
  /*  */
  const isUserLoggedIn = useSelector((state) => state.login.isUserLoggedIn);

  /*  */
  const navigate = useNavigate();

  const pageChangeHandler = () => {
    navigate("/service", {
      state: {
        funcType: "form",
        replyType: "",
        replyTitle: ` "${props.fetchProduct.product_name}" `,
        replyText: ` "${props.fetchProduct.product_name}" `,
      },
    });
  };

  return (
    <div className={classes.productSelectFunc}>
      <div
        className={`${classes.selectBtn} ${
          isUserLoggedIn ? null : classes.btnNotAllow
        } ${props.selectFunc === "" ? classes.selected : null}`}
        onClick={() => props.onSelectFunc("")}
      >
        <Image src="/images/UI/Product/button/like.svg" />
        <span></span>
      </div>
      <div
        className={`${classes.selectBtn} ${
          props.selectFunc === "" ? classes.selected : null
        }`}
        onClick={() => props.onSelectFunc("")}
      >
        <Image src="/images/UI/Product/button/comment.svg" />
        <span></span>
      </div>
      <div
        className={`${classes.selectBtn} ${
          props.selectFunc === "" ? classes.selected : null
        }`}
        onClick={() => props.onSelectFunc("")}
      >
        <Image src="/images/UI/Product/button/happy.svg" />
        <span></span>
      </div>
      <div
        className={`${classes.selectBtn} ${
          isUserLoggedIn ? null : classes.btnNotAllow
        } ${props.selectFunc === "" ? classes.selected : null}`}
        onClick={pageChangeHandler}
      >
        <Image src="/images/UI/Product/button/service.svg" />
        <span></span>
      </div>
    </div>
  );
};

export default ProductSelectFunc;
