import { createSlice } from "@reduxjs/toolkit";
import { IS_LOGGED_IN_KEY } from "../constants";

/*  */
const initialState = {
  isUserLoggedIn: false,
  userInfo: null,
};

/*  slice */
//  reducer  action creators
const loginSlice = createSlice({
  name: "LOGIN",
  initialState,
  reducers: {
    login: (state, action) => {
      state.isUserLoggedIn = true; // 
      state.userInfo = action.payload; // Redux Toolkit()
      sessionStorage.setItem(IS_LOGGED_IN_KEY, "1"); // session
    },
    logout: (state) => {
      state.isUserLoggedIn = false;
      state.userInfo = null;
      sessionStorage.removeItem(IS_LOGGED_IN_KEY);
    },

    // 
    updateUserInfo: (state, action) => {
      state.userInfo = { ...state.userInfo, ...action.payload };
    },
  },
});

// reducersloginlogout action creator
//  action creator  slice  actions 
export const { login, logout, updateUserInfo } = loginSlice.actions;

export default loginSlice.reducer;
