import React, { useState, useEffect } from "react";
import axios from "axios";
import { useSelector } from "react-redux";
import { API_URL_SERVICE } from "../../../../constants";

import UserServiceItem from "./UserServiceItem";

import classes from "./UserService.module.css";

const UserService = () => {
  /*  */
  const userInfo = useSelector((state) => state.login.userInfo);

  const [reply, setReply] = useState([]); // 
  const [editAllow, setEditAllow] = useState(false); // 
  const [editReply, setEditReply] = useState([]); //  (checkbox)
  const [refreshReply, setRefreshReply] = useState(false); //  ()

  /*  */
  useEffect(() => {
    const dataFetch = async () => {
      if (!userInfo) return alert("");
      try {
        const response = await axios.get(
          API_URL_SERVICE + `/service_select/?user_id=${userInfo.user_id}`
        );
        setReply(response.data);
      } catch (err) {
        console.error(" service_select:", err);
      }
    };
    dataFetch();
  }, [refreshReply]);

  /*  */
  const editChangeHandler = () => {
    setEditAllow((prev) => !prev);
    setEditReply([]);
  };

  /*  (checkbox) */
  const changeCheckboxHandler = (service_id, bool) => {
    //  inputChangeHandler  id  boolean
    // 1.  2. false  true 
    // ("""")
    if (bool) {
      //  true  state
      setEditReply([...editReply, service_id]);
    } else {
      //  true  false  ( true  false (by css))
      // 
      setEditReply(editReply.filter((id) => id !== service_id));
    }
  };

  /*  */
  const submitChangeHandler = async (e) => {
    e.preventDefault();
    if (editReply.length > 0) {
      try {
        const response = await axios.put(
          API_URL_SERVICE +
            `/service_is_resolved_update/?user_id=${userInfo.user_id}`,
          { service_id: editReply }
        );
        alert(response.data.message);
        setEditReply([]); // 
        setRefreshReply((prev) => !prev); //  refreshReply 
        editChangeHandler(); //  Edit
      } catch (err) {
        console.error(" service_is_resolved_update:", err);
      }
    } else {
      alert("");
    }
  };

  return (
    <form className={classes.userService}>
      <div className={classes.title_btn}>
        <span className={classes.title}></span>
        <div className={classes.btnContainer}>
          <span className={classes.amount}>
             {reply.length} {" "}
            {reply.filter((item) => item.is_resolved == 0).length} 
          </span>
          <button
            type="button"
            className={classes.editBtn}
            title=""
            onClick={editChangeHandler}
          >
            {editAllow ? "" : ""}
          </button>
          <button
            type="submit"
            className={`${classes.submitBtn} ${
              editAllow ? null : classes.notAllowSubmit
            }`}
            title=""
            onClick={submitChangeHandler}
          >
            
          </button>
        </div>
      </div>
      <div className={classes.separator}></div>
      {/*  */}
      <div
        className={`${classes.replyContainer} ${
          editAllow ? classes.allowEdit : null
        }`}
        title=""
      >
        {reply.map((item, index) => (
          <UserServiceItem
            key={index}
            reply={item}
            editAllow={editAllow}
            index={index + 1}
            onchangeCheckbox={changeCheckboxHandler}
          />
        ))}
      </div>
    </form>
  );
};

export default UserService;
