import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

import Image from "../Image/Image";
import classes from "./Footer.module.css";

const Footer = () => {
  /*  footer  */
  const location = useLocation(); // 
  const [showFooter, setShowFooter] = useState(true); //  footer
  const dontShow = ["/login", "/shoppingCart", "/user"]; //  footer
  const footer_black = ["/"]; //  footer

  useEffect(() => {
    setShowFooter(!dontShow.includes(location.pathname));
  }, [location.pathname]);

  return (
    <React.Fragment>
      {showFooter ? (
        <div
          className={`${classes.footer} ${
            footer_black.includes(location.pathname)
              ? classes.BgColorBlack
              : null
          }`}
        >
          {/*  */}
          <div className={classes.info}>
            <Image src="/images/UI/Nav/logo_text.svg" alt="logo" />
            <div className={classes.intro}>
              <p>TheGameC  | TheGameC </p>
              <p>TheGameCforTest123@gmail.com</p>
              <p>00-0000-0000</p>
            </div>
          </div>
          {/*  */}
          <div className={classes.link}>
            <div>
              <Image src="/images/UI/Footer/fb.svg" alt="FB logo" />
            </div>
            <div>
              <Image src="/images/UI/Footer/ig.svg" alt="IG logo" />
            </div>
            <div>
              <Image src="/images/UI/Footer/twitter.svg" alt="Twitter logo" />
            </div>
          </div>
        </div>
      ) : null}
    </React.Fragment>
  );
};

export default Footer;
