import React, { useState, useEffect } from "react";

import classes from "./Pagination.module.css";

const Pagination = (props) => {
  /*  */
  const [currentPage, setCurrentPage] = useState(1); // /

  /*  */
  const pageLimit = props.pageLimit || 6; // x
  const totalItems = props.items.length;
  const totalPages = Math.ceil(totalItems / pageLimit); //  ( / x)

  /* ex. */
  useEffect(() => {
    setCurrentPage(1);
  }, [props.resetPage]);

  /*  */
  const pageChangeHandler = (page) => {
    // 
    const startIndex = (page - 1) * pageLimit;
    const endIndex = startIndex + pageLimit;
    const PageShowItems = props.items.slice(startIndex, endIndex); //  items
    // 
    setCurrentPage(page);
    props.onGetPageItems(PageShowItems);
  };

  /*  +  */
  useEffect(() => {
    /*  pageLimit ex.  */
    const newTotalPages = Math.ceil(props.items.length / pageLimit); // newTotalPages  pageLimit 
    //  (*limit 366)
    if (currentPage > newTotalPages && newTotalPages > 0) {
      //  newTotalPages  0 currentPage  newTotalPages pageChangeHandler(0)
      //  newTotalPages  0 pageChangeHandler(currentPage);
      pageChangeHandler(newTotalPages);
    } else {
      /*  pageLimit  */
      pageChangeHandler(currentPage);
    }
  }, [currentPage, props.items, pageLimit]);

  return (
    <div className={classes.pagination}>
      <div>
        {Array.from({ length: totalPages }).map((_, index) => (
          <button
            key={index}
            onClick={() => pageChangeHandler(index + 1)}
            disabled={currentPage === index + 1}
            className={currentPage === index + 1 ? classes.currentPage : ""}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
};

export default Pagination;
