import React, { useState } from "react";
import axios from "axios";
import { API_URL_LOGIN } from "../../../constants";

import Dialog from "../../UI/Dialog";
import LoginInput from "../LoginInput";
import FullPageLoader from "../../UI/Loader/FullPageLoader";

import classes from "./PasswordForget.module.css";

const PasswordForget = (props) => {
  const [loader, setLoader] = useState(false); // Loader 

  /*  */
  const [newPasswordState, setNewPasswordState] = useState({
    username: "",
    verifyCode: "",
    newPassword: "",
    newPasswordVerify: "",
  });
  /*  */
  const [isVerifyCodeSubmit, setIsVerifyCodeSubmit] = useState(false);

  /* () */
  const [isValid, setIsValid] = useState({
    newPassword: false,
    newPasswordVerify: false,
  });

  /* onChange */
  const inputChangeHandler = (e) => {
    const { name, value } = e.target;
    setNewPasswordState((prev) => ({ ...prev, [name]: value }));
  };

  /*  */
  const inputVaildHandler = (name, valid) => {
    setIsValid((prev) => ({ ...prev, [name]: valid }));
  };

  /*  */
  const getVerifyCode = async () => {
    if (!newPasswordState.username) return alert("");
    setLoader(true); //  loader 
    try {
      const response = await axios.post(
        `${API_URL_LOGIN}/password_reset_mail_verify`,
        { username: newPasswordState.username }
      );
      setIsVerifyCodeSubmit(true);
      alert(response.data.message);
    } catch (err) {
      console.error(" password_reset_mail_verify:", err);
      alert(err.response.data.message || "");
    }
    setLoader(false); //  loader 
  };

  /*  */
  const submitFormHandler = async (e) => {
    e.preventDefault();
    // 
    if (Object.values(isValid).some((value) => !value))
      return alert("");
    // 
    if (newPasswordState.newPassword !== newPasswordState.newPasswordVerify)
      return alert("");
    try {
      const response = await axios.put(`${API_URL_LOGIN}/password_reset`, {
        passwordState: newPasswordState,
      });
      setNewPasswordState({
        username: "",
        verifyCode: "",
        newPassword: "",
        newPasswordVerify: "",
      });
      alert(response.data.message);
      props.onDialogClose();
    } catch (err) {
      console.error(" password_reset:", err);
      alert(err.response.data.message);
    }
  };

  return (
    <>
      {loader ? <FullPageLoader /> : null /*  loader */}
      <Dialog onDialogClose={props.onDialogClose}>
        <form
          className={classes.passwordForgetForm}
          onSubmit={submitFormHandler}
        >
          <span className={classes.logo}></span>
          <LoginInput
            type="text"
            img="user.svg"
            name="username"
            placeholder=""
            title=""
            value={newPasswordState.username}
            onChangeHandler={inputChangeHandler}
            readOnly={isVerifyCodeSubmit}
          />
          <button
            type="button"
            className={classes.getVerify}
            onClick={getVerifyCode}
          >
            
          </button>
          <LoginInput
            type="text"
            img="mail.svg"
            name="verifyCode"
            title=""
            placeholder=""
            value={newPasswordState.verifyCode}
            onChangeHandler={inputChangeHandler}
          />
          <LoginInput
            type="password"
            img="password.svg"
            name="newPassword"
            regex="^(?=.*[a-zA-Z])(?=.*[0-9])[a-zA-Z0-9]{6,}$"
            title="6"
            placeholder=""
            value={newPasswordState.newPassword}
            onChangeHandler={inputChangeHandler}
            onInputValid={inputVaildHandler}
          />
          <LoginInput
            type="password"
            img="password.svg"
            name="newPasswordVerify"
            regex="^(?=.*[a-zA-Z])(?=.*[0-9])[a-zA-Z0-9]{6,}$"
            title=""
            placeholder=""
            value={newPasswordState.newPasswordVerify}
            onChangeHandler={inputChangeHandler}
            onInputValid={inputVaildHandler}
          />
          <button className={classes.newPasswordBtn}></button>
        </form>
      </Dialog>
    </>
  );
};

export default PasswordForget;
