import React, { useState, useEffect } from "react";
import PItem from "./PItem";
import Pagination from "../../UI/Pagination";
import classes from "./ProductItems.module.css";

const ProductItems = (props) => {
  const [typeProducts, setTypeProducts] = useState([]); // 
  const [itemInCart, setItemInCart] = useState([]); // 

  /*  */
  //  Effect 
  useEffect(() => {
    //  & 
    let filteredProducts =
      props.selectProductType === ""
        ? props.dataFetch
        : props.dataFetch.filter((productItem) => {
            return productItem.product_type === props.selectProductType;
          });
    setTypeProducts(filteredProducts); // 
  }, [props.dataFetch, props.selectProductType]); // 

  /*  */
  const [pageLimit, setPageLimit] = useState(6); // 
  const [showItems, setShowItems] = useState([]); // 
  // ( Pagination )
  const pageItemsHandler = (items) => {
    setShowItems(items);
  };
  // 3
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    window.addEventListener("resize", handleResize);
    if (windowWidth < 750) {
      setPageLimit(3);
    } else {
      setPageLimit(6);
    }

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [windowWidth]);

  return (
    <div className={classes.productItems}>
      <div className={classes.ItemsContainer}>
        {showItems.map((item) => {
          return (
            <PItem
              key={"product_ID" + item.product_id}
              item={item}
              itemInCart={itemInCart}
              setItemInCart={setItemInCart}
              onAddtoCart={props.onAddtoCart}
              dataFetchCart={props.dataFetchCart}
              dataFetchOrder={props.dataFetchOrder}
            />
          );
        })}
      </div>
      <Pagination
        items={typeProducts}
        pageLimit={pageLimit}
        onGetPageItems={pageItemsHandler}
        resetPage={props.selectProductType}
        /*  */
      />
    </div>
  );
};

export default ProductItems;
