import React, { useEffect, useState } from "react";

import Pagination from "../UI/Pagination";

import classes from "./ServiceQA.module.css";

const QA = [
  {
    id: "1",
    q: "",
    ans: "//",
  },
  {
    id: "2",
    q: "",
    ans: " Express React  MySQL  MAMP  API ( axios) Redux ",
  },
  {
    id: "3",
    q: "",
    ans: " bcrypt.hash() ",
  },
  {
    id: "4",
    q: "?",
    ans: "",
  },
  {
    id: "5",
    q: "",
    ans: "",
  },
];

const ServiceQA = () => {
  /*  */
  let pageLimit = 3;
  const [showItems, setShowItems] = useState([]);
  const pageItemsHandler = (items) => {
    setShowItems(items);
  };

  return (
    <div className={classes.serviceQA}>
      <span className={classes.title}></span>
      {showItems.map((qa) => (
        <div key={qa.id} className={classes.QAcontainer}>
          <span className={classes.title}> {qa.id}</span>
          <span>Q{qa.q}</span>
          <div className={classes.ans}>
            <span>A{qa.ans}</span>
          </div>
        </div>
      ))}
      <Pagination
        items={QA}
        pageLimit={pageLimit}
        onGetPageItems={pageItemsHandler}
      />
    </div>
  );
};

export default ServiceQA;
