import React, { useState, useEffect } from "react";
import axios from "axios";
import ReactDOM from "react-dom";
import { API_URL } from "../../../constants";

import Image from "../Image/Image";
import Backdrop from "../Backdrop";
import SearchForm from "./SearchForm";
import SearchResult from "./SearchResult";

import classes from "./Search.module.css";

const Search = (props) => {
  const [searchResult, setSearchResult] = useState([]); // 
  const [allProduct, setAllProduct] = useState(); // 

  /*  */
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const response = await axios.get(
          `${API_URL}/products/api/products_select`
        );
        setAllProduct(response.data);
      } catch (err) {
        console.error(" products_select:", err);
      }
    };
    fetchProducts();
  }, []);

  /*  */
  const fetchSearchResult = async (keyword) => {
    try {
      const response = await axios.get(
        `${API_URL}/products/api/products_search?keyword=${keyword}`
      );
      setSearchResult(response.data);
    } catch (err) {
      console.error(" products_search:", err);
    }
  };

  // 
  return ReactDOM.createPortal(
    <div>
      <Backdrop onDialogClose={props.onDialogClose} title="" />
      <div className={classes.search}>
        {/*  */}
        <Image
          className={classes.closeBtn}
          src="/images/UI/common_button/cancel.svg"
          alt=""
          title=""
          onClick={props.onDialogClose}
        />
        {/*  bar +  */}
        <SearchForm onFetch={fetchSearchResult} fetchProducts={allProduct} />
        {searchResult ? (
          <SearchResult
            items={searchResult}
            setSearchState={props.setSearchState}
          />
        ) : null}
      </div>
    </div>,
    document.getElementById("portal-root") //  index.html #portal-root 
  );
};

export default Search;
