import React, { useEffect, useState } from "react";
import axios from "axios";
import { API_URL_SERVICE } from "../../constants";
import { useSelector } from "react-redux";

import classes from "./ServiceForm.module.css";

const ServiceForm = (props) => {
  /*  */
  const isUserLoggedIn = useSelector((state) => state.login.isUserLoggedIn);
  const userInfo = useSelector((state) => state.login.userInfo);

  /*  */
  const [form, setForm] = useState({
    category: "",
    subject: "",
    description: "",
  });

  /*  */
  useEffect(() => {
    setForm({
      category: props.reply.replyType,
      subject: props.reply.replyTitle,
      description: props.reply.replyText,
    });
  }, [props.reply]);

  /*  */
  const formChangeHandler = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  /*  */
  const submitHandler = async (e) => {
    e.preventDefault();
    if (isUserLoggedIn && userInfo) {
      if (form.category && form.description && form.subject) {
        if (form.category === "") return alert("");
        try {
          const response = await axios.post(
            `${API_URL_SERVICE}/service_insert/?user_id=${userInfo.user_id}`,
            form
          );
          alert(response.data.message);
          setForm({
            category: "",
            subject: "",
            description: "",
          });
        } catch (err) {
          alert(err.response.data.message);
        }
      } else {
        alert("");
      }
    } else {
      alert("");
    }
  };

  return (
    <form className={classes.serviceForm} onSubmit={submitHandler}>
      {isUserLoggedIn ? null : (
        <span style={{ color: "red" }}></span>
      )}
      <span className={classes.title}></span>
      {/*  */}
      <div className={classes.inputContainer}>
        <span className={classes.title}></span>
        <select
          className={isUserLoggedIn ? null : classes.noAllow}
          onChange={formChangeHandler}
          name="category"
          value={form.category}
        >
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      {/*  */}
      <div className={classes.inputContainer}>
        <span className={classes.title}></span>
        <input
          className={isUserLoggedIn ? null : classes.noAllow}
          onChange={formChangeHandler}
          placeholder=" (25)"
          name="subject"
          maxLength={25}
          value={form.subject}
        />
      </div>
      {/*  */}
      <div className={classes.inputContainer}>
        <span className={classes.title}></span>
        <textarea
          className={isUserLoggedIn ? null : classes.noAllow}
          onChange={formChangeHandler}
          placeholder=" (1000)"
          name="description"
          maxLength={1000}
          value={form.description}
        />
      </div>
      <button
        type="submit"
        className={`${classes.submitBtn} ${
          isUserLoggedIn ? null : classes.noAllow
        }`}
      >
        
      </button>
    </form>
  );
};

export default ServiceForm;
